import React, { useState, useEffect, useRef } from 'react';
import useCustomMove from '../../hooks/useCustomMove';
import { postAddBuy } from '../../api/buyApi';
import ResultModal from '../common/ResultModal';
import PostComponent from '../common/PostComponent';
import { getUser } from './../../api/userApi';
import { useSelector } from 'react-redux';
import iconNext from '../../resources/images/icon-next.png';
import iconEdit from '../../resources/images/iconEdit.png';
import { postCreateRoom } from '../../api/chatApi';

const initState = {
  id: 0,
  nickname: '',
  title: '',
  location: '',
  content: '',
  buyCategory: '',
  max: 2,
  current: 1,
  deadline: '',
  buyHit: 0,
  files: [],
};

const AddComponent = () => {
  const { moveToList } = useCustomMove();
  const [postImageFiles, setPostImageFiles] = useState([]); // ì´ë¯¸ì§€ íŒŒì¼ í”„ë¦¬ë·°
  const [buy, setBuy] = useState({ ...initState });
  const [result, setResult] = useState(null);
  const [addResultModal, setAddResultModal] = useState(null);
  const imgRef = useRef();
  const [user, setUser] = useState(initState);
  const loginInfo = useSelector((state) => state.loginSlice); // ì „ì—­ìƒíƒœì—ì„œ loginSliceëŠ” ë¡œê·¸ì¸ ì‚¬ìš©ìì˜ ìƒíƒœì •ë³´
  const ino = loginInfo.id;
  const [location, setLocation] = useState(null); // í˜„ì¬ ìœ„ì¹˜ë¥¼ ì €ì¥í•  ìƒíƒœ

  useEffect(() => {
    getUser(ino).then((data) => {
      setUser(data);
    });
  }, [ino]);

  const handleImageChange = (e) => {
    // ì´ë¯¸ì§€ ë³€ê²½
    const files = Array.from(e.target.files);
    const nonImageFiles = files.filter((file) => !file.type.startsWith('image/'));
    if (nonImageFiles.length > 0) {
      setAddResultModal('ì´ë¯¸ì§€ íŒŒì¼ë§Œ ë“±ë¡ ê°€ëŠ¥í•©ë‹ˆë‹¤');
      imgRef.current.value = '';
      setPostImageFiles([]);
      return;
    }
    const imagePreviews = files.map((file) => ({
      url: URL.createObjectURL(file),
      name: file.name,
    }));
    setPostImageFiles(imagePreviews);
  };

  const handleRemoveImage = (index) => {
    // ì´ë¯¸ì§€ ì œê±°
    setPostImageFiles((postFiles) => postFiles.filter((_, i) => i !== index));
    updateFileInput(index);
  };

  const updateFileInput = (removeIndex) => {
    // ì…ë ¥ëœ íŒŒì¼ ì—…ë°ì´íŠ¸
    const dataTransfer = new DataTransfer();
    const files = Array.from(imgRef.current.files);
    files.forEach((file, index) => {
      if (index !== removeIndex) {
        //removeIndexì™€ ì¼ì¹˜í•˜ì§€ ì•ŠëŠ” íŒŒì¼ë§Œ dataTransferì— ì¶”ê°€
        dataTransfer.items.add(file);
      }
    });
    imgRef.current.files = dataTransfer.files; //ì œê±°ëœ íŒŒì¼ ë°˜ì˜
  };

  const handleChangeBuy = (e) => {
    const { name, value } = e.target;
    setBuy((prev) => ({ ...prev, [name]: value }));
  };

  const handleGeocode = () => {
    return new Promise((resolve, reject) => {
      const { kakao } = window;
      const geocoder = new kakao.maps.services.Geocoder();
      geocoder.addressSearch(buy.location, function (result, status) {
        if (status === kakao.maps.services.Status.OK) {
          const coords = new kakao.maps.LatLng(result[0].y, result[0].x);
          setLocation({
            latitude: coords.getLat(),
            longitude: coords.getLng(),
          });
          resolve({
            latitude: coords.getLat(),
            longitude: coords.getLng(),
          });
        } else {
          setAddResultModal('ì£¼ì†Œ ë³€í™˜ì— ì‹¤íŒ¨í–ˆìŠµë‹ˆë‹¤.');
          reject(new Error('Failed to geocode the address.'));
        }
      });
    });
  };

  const handleClickAdd = async () => {
    try {
      const { latitude, longitude } = await handleGeocode();

      if (!buy.buyCategory) {
        setAddResultModal('ì¹´í…Œê³ ë¦¬ë¥¼ ì„ íƒí•´ì£¼ì„¸ìš”');
        return;
      }
      if (!buy.title || !buy.content) {
        setAddResultModal('ì œëª©ê³¼ ë‚´ìš©ì„ ì…ë ¥í•´ì£¼ì„¸ìš”');
        return;
      }
      if (!buy.deadline || !buy.location) {
        setAddResultModal('ë§ˆê°ì‹œê°„ê³¼ ëª¨ì„ì¥ì†Œë¥¼ ì…ë ¥í•´ì£¼ì„¸ìš”');
        return;
      }
      const time = new Date();
      const timeElement = new Date(buy.deadline);
      if (time > timeElement) {
        setAddResultModal('í˜„ì¬ ì‹œê°„ë³´ë‹¤ ì´ì „ì˜ ë‚ ì§œëŠ” ì„¤ì •í•  ìˆ˜ ì—†ìŠµë‹ˆë‹¤');
        return;
      }
      const files = imgRef.current.files;
      const formData = new FormData();
      for (let i = 0; i < files.length; i++) {
        formData.append('files', files[i]);
      }
      formData.append('id', ino);
      formData.append('nickname', user.nickname);
      formData.append('title', buy.title);
      formData.append('location', buy.location);
      formData.append('latitude', latitude); // ìœ„ë„
      formData.append('longitude', longitude); // ê²½ë„
      formData.append('content', buy.content);
      formData.append('buyCategory', buy.buyCategory);
      formData.append('max', buy.max);
      formData.append('current', buy.current);
      formData.append('deadline', buy.deadline);
      formData.append('buyHit', buy.buyHit);
      const response = await postAddBuy(formData); // ë“±ë¡ ë²„íŠ¼ í´ë¦­ì‹œ ë””ë¹„ì— ì €ì¥ í›„ buyNo ê°€ì ¸ì˜¤ê¸°
      const createRequest = { buyNo: response.buyNo };
      await postCreateRoom(formData.get('id'), formData.get('title'), 'ê³µë™êµ¬ë§¤', createRequest);
      setResult('ê²Œì‹œê¸€ì´ ë“±ë¡ë˜ì—ˆìŠµë‹ˆë‹¤');
    } catch (error) {
      console.error('Error adding post:', error);
      setAddResultModal('ê²Œì‹œê¸€ ë“±ë¡ì— ì‹¤íŒ¨í–ˆìŠµë‹ˆë‹¤.');
    }
  };

  const closeModal = () => {
    setResult(null);
    moveToList();
  };

  const setAddress = (address) => {
    setBuy((prev) => ({ ...prev, location: address }));
  };

  const handleInputValidation = (e) => {
    const value = e.target.value;
    if (value !== '' && (isNaN(value) || value < 2)) {
      e.target.value = Math.max(2, value);
    }
    handleChangeBuy(e);
  };

  return (
    <div>
      <div className="flex items-center w-1/2 mx-auto text-xl font-semibold pl-2 border-l-4 border-teal-300">
        ê³µë™êµ¬ë§¤ <img src={iconNext} className="w-7 mx-2" alt="Next Icon" /> ê¸€ ì‘ì„±
      </div>
      <div className="grid grid-cols-8 gap-3 w-1/2 mx-auto mt-2 p-2 text-xl shadow-set mb-5">
        <div className="col-start-3 col-span-4 mt-2 border rounded overflow-x-scroll whitespace-nowrap">
          {postImageFiles.map((file, index) => (
            <div key={index} className="relative m-3 w-36 h-36 inline-block align-top border">
              <img src={file.url} alt={file.name} className="w-36 h-36 object-cover" />
              <button
                type="button"
                onClick={() => handleRemoveImage(index)}
                className="absolute top-2 right-2 bg-slate-500 text-white rounded-full w-6 h-6 flex items-center justify-center hover:bg-slate-600"
                aria-label="Remove image"
              >
                X
              </button>
            </div>
          ))}
          <div className="relative inline-block m-3 w-36 h-36 border-2 border-gray-300 border-dashed rounded-md items-center justify-center cursor-pointer hover:bg-slate-100">
            <input ref={imgRef} id="file-upload" type="file" multiple={true} onChange={handleImageChange} className="absolute inset-0 opacity-0 cursor-pointer" />
            <label htmlFor="file-upload" className="text-mainColor font-bold text-4xl flex flex-col items-center justify-center h-full w-full">
              +<span className="text-base mt-1">ì´ë¯¸ì§€ ì²¨ë¶€</span>
            </label>
          </div>
        </div>
        <div className="col-start-2 col-span-2">
          <label htmlFor="buyCategory" className="flex items-center text-sm font-medium text-gray-700 mb-1">
            <img src={iconEdit} className="w-3 h-3" alt="edit"></img>&nbsp;ì¹´í…Œê³ ë¦¬
          </label>
          <select id="buyCategory" name="buyCategory" className="w-full pl-2 h-9 rounded-md border border-stone-400 text-base" value={buy.buyCategory} onChange={handleChangeBuy}>
            <option disabled hidden value="">
              ì¹´í…Œê³ ë¦¬ ì„ íƒ
            </option>
            <option value="1">ë°°ë‹¬ìŒì‹</option>
            <option value="2">ìƒí•„í’ˆ</option>
            <option value="3">ì‹ë£Œí’ˆ</option>
            <option value="4">ê°€êµ¬/ê°€ì „</option>
            <option value="5">ê¸°íƒ€</option>
          </select>
        </div>
        <div className="col-start-5 col-span-1">
          <label htmlFor="max" className="flex items-center text-sm font-medium text-gray-700 mb-1">
            <img src={iconEdit} className="w-3 í•˜-3" alt="edit"></img>&nbsp;ëª¨ì§‘ì¸ì›
          </label>
          <input
            type="number"
            name="max"
            id="max"
            value={buy.max}
            min="2"
            step="1"
            onInput={handleInputValidation}
            onChange={handleChangeBuy}
            className="w-full h-9 pl-2 rounded-md border border-stone-400 text-base"
          />
        </div>
        <div className="col-start-6 col-span-2">
          <label htmlFor="deadline" className="flex items-center text-sm font-medium text-gray-700 mb-1">
            <img src={iconEdit} className="w-3 í•˜-3" alt="edit"></img>&nbsp;ëª¨ì§‘ë§ˆê°ì‹œê°„
          </label>
          <input
            type="datetime-local"
            required
            aria-required="true"
            name="deadline"
            id="deadline"
            value={buy.deadline}
            onChange={handleChangeBuy}
            className="w-full h-9 rounded-md border border-stone-400 text-base"
          />
        </div>
        <div className="col-start-2 col-span-6">
          <label htmlFor="title" className="flex items-center text-sm font-medium text-gray-700 mb-1">
            <img src={iconEdit} className="w-3 í•˜-3" alt="edit"></img>&nbsp;ì œëª©
          </label>
          <input
            type="text"
            name="title"
            id="title"
            value={buy.title}
            onChange={handleChangeBuy}
            placeholder="ì œëª©ì„ ì…ë ¥í•˜ì„¸ìš”"
            className="w-full h-9 pl-2 rounded-md border border-stone-400 placeholder:text-base pb-1"
          />
        </div>
        <div className="col-start-2 col-span-6">
          <label className="flex items-center text-sm font-medium text-gray-700 mb-1">
            <img src={iconEdit} className="w-3 í•˜-3" alt="edit"></img>&nbsp;ê±°ë˜ì¥ì†Œ
          </label>
          <div className="w-1/5 ml-1 text-base">
            <PostComponent setAddress={setAddress}></PostComponent>
          </div>
          <div className="flex">
            <div className="w-4/5 pl-1">
              <input
                className="w-full h-10 pl-2 rounded-md border border-stone-400 placeholder:text-base pb-1"
                name="addr"
                type="text"
                placeholder="ì£¼ì†Œ(ìš°í¸ë²ˆí˜¸ ë° ë„ë¡œëª… ê²€ìƒ‰)"
                value={buy.location}
                onChange={handleChangeBuy}
              />
            </div>
          </div>
        </div>
        <div className="col-start-2 col-span-6">
          <label htmlFor="content" className="flex items-center text-sm font-medium text-gray-700 mb-1">
            <img src={iconEdit} className="w-3 í•˜-3" alt="edit"></img>&nbsp;ë‚´ìš©
          </label>
          <textarea
            name="content"
            id="content"
            value={buy.content}
            rows="6"
            onChange={handleChangeBuy}
            placeholder="ë‚´ìš©ì„ ì…ë ¥í•˜ì„¸ìš”"
            className="w-full pl-2 rounded-md border border-stone-400 placeholder:text-base pb-1"
          ></textarea>
        </div>
        <div className="col-start-6 col-span-2 mb-3">
          <div className="flex">
            <button className="text-base text-white bg-mainColor p-2 rounded-md w-1/2 mr-2 hover:bg-emerald-600" onClick={handleClickAdd}>
              ë“±ë¡í•˜ê¸°
            </button>
            <button className="text-base text-white bg-slate-300 p-2 rounded-md w-1/2 hover:bg-slate-400" onClick={() => moveToList()}>
              ëª©ë¡
            </button>
          </div>
        </div>
        {result && <ResultModal title={'ì•Œë¦¼'} content={`${result}`} callbackFn={closeModal} />}
        {addResultModal && <ResultModal title={'ì•Œë¦¼'} content={`${addResultModal}`} callbackFn={() => setAddResultModal(null)} />}
      </div>
    </div>
  );
};

export default AddComponent;
